<template>
  <div>
    <div class="box">

      <div id="app" ref="three"></div>
    </div>
    <div>
      rotateX:<input type="number" v-model="x"><br/>
      rotateY:<input type="number" v-model="y"><br/>
      rotateZ:<input type="number" v-model="z"><br/>
    </div>
    <div>
      x.speed:{{sx}}
      <button @click="speedAdd('sx',1)">+</button>
      <button @click="speedAdd('sx',-1)">-</button><br/>
      y.speed:{{sy}}
      <button @click="speedAdd('sy',1)">+</button>
      <button @click="speedAdd('sy',-1)">-</button><br/>
      z.speed:{{sz}}
      <button @click="speedAdd('sz',1)">+</button>
      <button @click="speedAdd('sz',-1)">-</button><br/>
      <button @click="startMove">start</button>
      <button @click="stop">stop</button>
    </div>
  </div>
</template>

<script>
  import MakeTh from './three'
  export default {
    name: "Three",
    data(){
      return {
        x:0,
        y:0,
        z:0,
        sx:0,
        sy:1,
        sz:1,
        isGo:true,
      }
    },
    methods:{
      startMove(){
        clearInterval(this.timer);
        this.timer=setInterval(()=>{
          this.x=Math.floor(this.x)+this.sx;
          this.y=Math.floor(this.y)+this.sy;
          this.z=Math.floor(this.z)+this.sz;
          this.$refs.three.style.transform=`rotateX(${this.x}deg)
                                            rotateY(${this.y}deg)
                                            rotateZ(${this.z}deg)
                                            `;
        },1000/80);
        this.isGo=true;
      },
      stop(){
        clearInterval(this.timer);
        this.isGo=false;
      },
      speedAdd(obj,num){
        this[obj]+=num;
      }
    },
    mounted() {
      new MakeTh(this.$refs.three);
      this.startMove();
    },
    deactivated(){
      let a=this.isGo;
      this.stop();
      a?this.isGo=true:'';
    },
    activated(){
      this.isGo?this.startMove():'';
    }
  }
</script>

<style scoped>

  .box{

    perspective: 0;
    position: relative;
    width: 100px;
    height: 100px;
    margin:100px auto;
  }
  #app{
    transform: rotateX(0deg) rotateY(0deg);
  }
</style>
